<template>
  <div class="registered">
    <div class="center">
      <van-field v-model="nickname" label="昵称" />
      <!-- 输入手机号，调起手机号键盘 -->
      <van-field v-model="password" label="密码" />
      <van-field v-model="phone" type="tel" label="手机号码" />
      <van-field
        v-model="sms"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <template #button>
          <van-button
            size="small"
            type="primary"
            :disabled="canSend"
            @click="captcha"
            color="linear-gradient(to right, #ff6034, #ee0a24)"
            >{{ txtTips }}</van-button
          >
        </template>
      </van-field>
      <van-button round type="info" block @click="registered">注册</van-button>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  data () {
    return {
      nickname: '',
      password: '',
      phone: '',
      sms: '',
      canSend: false,
      total: 60,
      txtTips: '获取验证码',
    }
  },
  methods: {
    // 验证码
    captcha () {
      let phone = this.phone;
      var phoneValid = /^[1][3,4,5,7,8,9][0-9]{9}$/;
      if (phone == '' || !phoneValid.test(phone)) {
        Toast('请正确输入手机号码!');
        return false;
      } else {
        console.log(0)
      }
      this.canSend = true;
      let clock = window.setInterval(() => {
        this.total--
        this.txtTips = this.total + '秒'
        if (this.total == 0) {
          this.canSend = false;
          clearInterval(clock)
          this.total = 60;
          this.txtTips = '获取验证码'
        }
      }, 1000)
      let data = {
        phone: phone
      }
      this.api.captcha(data).then(res => {
        console.log(res)
      }).catch(err => {
        console.log(err)
      })
    },
    // 注册
    registered () {
      let data = {
        captcha: this.sms,
        phone: this.phone,
        password: this.password,
        nickname: this.nickname
      }
      this.api.registered(data).then(res => {
        if (res.data.code == 200) {
          Toast(res.data.message)
          this.$router.push('/login')
        } else {
          Toast(res.data.message)
        }
      }).catch(err => {
        console.log(err)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.registered {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../../assets/img/bg.png") no-repeat center;
  background-size: 100% 100%;
  .center {
    margin: 0 20px;
    padding: 16px;
    background: #fff;
    border-radius: 8px;
    overflow: hidden;

    /deep/ .van-cell {
      padding: 10px 0;
    }
  }
}
</style>